import { Paper, makeStyles } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { addUserAPI, updateUserAPI } from 'src/actions/users'
import { PopupForm } from 'src/views/utils/controls/PopupForm'
import { UserForm } from './UserForm'
import UsersGrid from './UsersGrid'

const useStyles = makeStyles((theme) => ({
  pageContent: {
    margin: theme.spacing(3),
    padding: theme.spacing(3),
  },
  searchInput: {
    width: '75%',
  },
}))

const Users = () => {
  const dispatch = useDispatch()
  const [openPopup, setOpenPopup] = useState(false)
  const [recordForEdit, setRecordForEdit] = useState(null)
  const classes = useStyles()

  const addOrEdit = (user, resetForm) => {
    if (user.id === 0) {
      delete user.id
      dispatch(addUserAPI(user))
    } else {
      delete user.tableData
      dispatch(updateUserAPI(user, user.id))
    }

    resetForm()
    setRecordForEdit(null)
    setOpenPopup(false)
  }

  const cancelForm = (resetForm) => {
    resetForm()
    setRecordForEdit(null)
    setOpenPopup(false)
  }
  return (
    <>
      <Paper>
        <UsersGrid setOpenPopup={setOpenPopup} setRecordForEdit={setRecordForEdit} />
      </Paper>
      <PopupForm
        openPopup={openPopup}
        setOpenPopup={setOpenPopup}
        setRecordForEdit={setRecordForEdit}
        title="Usuario"
      >
        <UserForm recordForEdit={recordForEdit} addOrEdit={addOrEdit} cancelForm={cancelForm} />
      </PopupForm>
    </>
  )
}
export default Users
