import axios from 'axios'
import { types } from '../types/types'

export const getAllUsers = () => {
  return async (dispatch) => {
    await axios
      .get(`${process.env.REACT_APP_API_URL_BASE}/api/users`)
      .then((response) => {
        console.log(response)

        dispatch(getUsers(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const addUserAPI = (user) => {
  return async (dispatch) => {
    await axios
      .post(`${process.env.REACT_APP_API_URL_BASE}/api/users`, user)
      .then((response) => {
        dispatch(createUser(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const updateUserAPI = (user, id) => {
  return async (dispatch) => {
    await axios
      .put(`${process.env.REACT_APP_API_URL_BASE}/api/users/${id}`, user)
      .then((response) => {
        dispatch(updateUser(user))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const createUser = (user) => ({
  type: types.createUser,
  payload: user,
})

export const updateUser = (user) => ({
  type: types.updateUser,
  payload: user,
})

export const getUsers = (users) => ({
  type: types.getUsers,
  payload: users,
})
