import React, { useState, useEffect } from 'react'
import { useForm as customForm, Form } from 'src/layout/useForm'
import Controls from 'src/views/utils/controls/Controls'
import { useSelector } from 'react-redux'
import { Grid } from '@mui/material'
// import { FormRadioGroup } from 'src/views/utils/controls/FormRadioGroup'

// const genderItems = [
//   { id: 'male', title: 'Male' },
//   { id: 'female', title: 'Female' },
//   { id: 'other', title: 'Other' },
// ]

const initialFValues = {
  id: 0,
  name: '',
  lastname: '',
  roleId: '',
  branchId: '',
  phone: '',
  email: '',
  password: '',
  birth_date: new Date(),
  address: '',
}

export const UserForm = (props) => {
  const { recordForEdit, addOrEdit, cancelForm } = props

  const { values, setValues, handleInputChange, resetForm } = customForm(initialFValues)
  const branches = useSelector((state) => state.branches)
  const roles = useSelector((state) => state.roles)

  useEffect(() => {
    if (recordForEdit != null) {
      console.log(recordForEdit)
      if (recordForEdit.id) setValues({ ...recordForEdit })
    }
  }, [recordForEdit])

  const handleSubmit = (e) => {
    e.preventDefault()
    addOrEdit(values, resetForm)
  }

  const handleReset = () => {
    resetForm()
  }

  const handleCancel = () => {
    cancelForm(resetForm)
  }
  return (
    <Form onSubmit={handleSubmit}>
      <Grid container rowSpacing={0} columnSpacing={2}>
        <Grid item xs={6} sm={3}>
          <Controls.FormInput
            label="Nombre"
            name="name"
            value={values.name}
            onChange={handleInputChange}
            required
          />
        </Grid>
        <Grid item xs={6} sm={3}>
          <Controls.FormInput
            label="Apellido(s)"
            name="lastname"
            value={values.lastname}
            onChange={handleInputChange}
            required
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Controls.FormSelect
            name="branchId"
            label="Sucursal"
            value={values.branchId}
            onChange={handleInputChange}
            items={recordForEdit?.id ? branches : branches.filter((e) => e.status)}
            required
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Controls.FormInput
            label="Teléfono"
            name="phone"
            value={values.phone}
            onChange={handleInputChange}
            required
          />
          <Controls.FormInput
            label="Dirección"
            name="address"
            value={values.address}
            onChange={handleInputChange}
            required
          />

          <Controls.FormDatePicker
            name="birth_date"
            label="Fecha de nacimiento"
            value={values.birth_date}
            onChange={handleInputChange}
            required
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Controls.FormSelect
            name="roleId"
            label="Rol"
            value={values.roleId}
            onChange={handleInputChange}
            items={recordForEdit?.id ? roles : roles.filter((e) => e.status)}
            required
          />

          <Controls.FormInput
            label="Correo electronico"
            name="email"
            value={values.email}
            onChange={handleInputChange}
            validators={['isEmail']}
            errorMessages={['Ingresar email válido']}
            required
          />

          <Controls.FormInput
            label="Contraseña"
            name="password"
            value={values.password}
            onChange={handleInputChange}
            required
            disabled={recordForEdit && true}
          />
          {/* <Controls.FormRadioGroup
            name="gender"
            label="Género"
            value={values.gender}
            onChange={handleInputChange}
            items={genderItems}
          /> */}
        </Grid>

        <Grid item xs={12} style={{ textAlign: 'right', paddingTop: 5 }}>
          <Controls.FormButton
            variant="text"
            color="default"
            size="large"
            text="Cancelar"
            onClick={handleCancel}
          />
          <Controls.FormButton
            variant="contained"
            color="primary"
            type="submit"
            size="large"
            text="Guardar"
          />
          {/* <Controls.FormButton
            variant="contained"
            color="default"
            size="large"
            text="Limpiar"
            onClick={handleReset}
          /> */}
        </Grid>
      </Grid>
    </Form>
  )
}
